import React from "react";
import ListGroup from 'react-bootstrap/ListGroup';
import Container from "react-bootstrap/Container"

function FreeOffer() {

    return (
        <Container>
        <div>
            <h1 className="text-center">How to Hire a Software Talent Even If You Don't Have Enough Time: Discover How Quickly You Can Hire A Software Developer Talent</h1>
            <h2>Dear Business owner,</h2>
            <p>Yes I know how you feel when It's time to get the right software talent and add to your team. And you have got a week to go through thousands of applicants' CVs or resumes.</p>
            <p>Let’s face it, preparing and getting through the hiring process on your own is frustrating at best. You would rather spend that beautiful weekend with friends and family, so let our firm handle your hiring process for you this year</p>
            <p>All you need to do is to get the contact form filled and we will take it from there to get you the right people to fill the position for you. Besides, we take care of your search, listing, veting, training and interviewing before handing over the candidates for any further assessment before a given offer.</p>
            <p>Imagine what it will be like to be able to hire the right software talents for your team. What problem does engaging Elihr's services solve? Well, It will save you a lot of money involved in the recruiting process, and put a stop to outsourcing part of your software development. It will also save you time that you would have invested in other profitable parts of your business processes.</p>
            <p>I am a software developer/engineer certified by IBM and Udacity. I have been part of the recruiting team of the previous companies I had worked for. Besides I had helped several businesses with advice on how best to recruit the right software developer/engineer talent as part of their teams.</p>
            <p>I discovered that many business owners are facing the problem of the high cost of hiring coupled with marketing expenses associated with advertising the position to the right candidate. So I decided to help solve it in my own way</p>
            <ListGroup as="ol" numbered >
                <ListGroup.Item as="li">Well, It will save you a lot of money involved in the recruiting process,</ListGroup.Item>
                <ListGroup.Item as="li">and put a stop to outsourcing part of your software development.</ListGroup.Item>
                <ListGroup.Item as="li">It will also save you time that you would have invested in</ListGroup.Item>
                <ListGroup.Item as="li">It will save you money you would have spent in advertising the position</ListGroup.Item>
                <ListGroup.Item as="li">It will save from drudgery or exhausting tedious work</ListGroup.Item>
                <ListGroup.Item as="li">Provide you with the workers</ListGroup.Item>
            </ListGroup>
            <p>How do we work? To briefly take you through how we work, we </p>


            <ListGroup as="ol" numbered>
                <ListGroup.Item as="li">Search for right candidates</ListGroup.Item>
                <ListGroup.Item as="li">Collect and list the candidates</ListGroup.Item>
                <ListGroup.Item as="li">Select the best from the list</ListGroup.Item>
                <ListGroup.Item as="li">Vet them thoroughly</ListGroup.Item>
                <ListGroup.Item as="li">Make them to take technical test</ListGroup.Item>
                <ListGroup.Item as="li">Follow the test with technical interview</ListGroup.Item>
                <ListGroup.Item as="li">Then followed by behavioural and/or situational interview</ListGroup.Item>
                <ListGroup.Item as="li">Assist in salary negotiation</ListGroup.Item>
                <ListGroup.Item as="li">Finally we hand them over to any further review or assessment</ListGroup.Item>
            </ListGroup>

            <p>We have helped several small business owners to hire the right software talent. Here is what they have to say:</p>

            <ListGroup as="ol" numbered>
                <ListGroup.Item as="li">Thank you. I'm pleased with his service. Idris Daniel, Owner of Danimart</ListGroup.Item>
                <ListGroup.Item as="li">I have less worry now just by having the person. Thank you. Muhd Zubair, Owner of Hycare LTD</ListGroup.Item>
                <ListGroup.Item as="li">I was able to get my project complete and deliver to the customers. Like your service. Thanks. Mal. Mustapha Ojo, Owner of Binaman Fishery</ListGroup.Item>
            </ListGroup>
            
            <p>We will deliver to or provide you with quality and professional workers and staffs who has been vetted and passed through our technical, non technical assessment and interviews. How long is it going to take us to deliver? It's within a week. How much is it going to cost you? You only pay us ₦59,000. Which methods are we going to use to pay you? We accept all methods of payment.</p>

            <p>Here is what you'll get if you take this offer now:</p>

            <ListGroup as="ol" numbered>
                <ListGroup.Item as="li">One-time Setup Fee: ₦10,000</ListGroup.Item>
                <ListGroup.Item as="li">Service Maintenance Fee: ₦59,000 per month</ListGroup.Item>
                <ListGroup.Item as="li">BONUS: FREE share of contents on market and industry knowledge (valued at ₦250,000)</ListGroup.Item>
                <ListGroup.Item as="li">BONUS: FREE support services in case of any disruption of service (valued at ₦100,000)</ListGroup.Item>
                <ListGroup.Item as="li">BONUS: FREE support services in case of any disruption of service (valued at ₦100,000)</ListGroup.Item>
                <ListGroup.Item as="li">BONUS: FREE training for any of our accepted workers and staffs (valued at ₦100,000)</ListGroup.Item>
                <ListGroup.Item as="li">BONUS: FREE Advice to clients on employment issues and labour laws (₦50,000)</ListGroup.Item>
                <ListGroup.Item as="li">Grand Total is : ₦569,000 for everything</ListGroup.Item>
            </ListGroup>

            <p>But right now you can have all these today set up by paying N59,000 monthly only. Are you surprised? Don't be. We mean it.</p>
            
            <p>Register to take this offer now. We are taking only 10 prospects so that we can render effective service to them. So be the first.</p>
            
            <p>If after we have set it up for you or engaged us and you are dissatisfied or not pleased, you can cancel the monthly maintenance fee at any time by just sending us an email or call.</p>
            
            <p>What are you waiting for? Enter your email address and tap next to get started now</p>
        </div>
        </Container>
    )
   
}

export default FreeOffer;

